<template>
  <list-page
    :title="$t('settings.account.phone.title')"
    :tip="$t('settings.account.phone.tip')"
    :button="$t('btn.next')"
    :disable="disable"
    @click="toNext"
  >
    <template #page>
      <transition name="slide-wrapper">
        <router-view :verify="verify" style="z-index: 9;"/>
      </transition>
    </template>
    <phone
      v-model="phone"
      :area-code="areaCode"
      :error="error"
      @change="onAreaCodeChange"
      @input="clean"
    />
  </list-page>
</template>

<script>
import { checkPhone } from '@/utils/patt.js'
import ListPage from '@/components/list/ListPage.vue'
import Phone from '@/components/list/Phone.vue'

export default {
  components: {
    ListPage,
    Phone
  },
  data () {
    return {
      verify: null,
      areaCode: '',
      phone: '',
      regExp: '',
      error: ''
    }
  },
  computed: {
    disable () {
      return !this.areaCode || !this.phone
    }
  },
  methods: {
    clean () {
      this.error = ''
    },
    onAreaCodeChange ({ areaCode, regExp }) {
      this.areaCode = areaCode
      this.regExp = regExp
      this.clean()
    },
    toNext () {
      let result = checkPhone(this.areaCode, this.phone, this.regExp)
      this.error = result.msg
      if (result.pass) {
        let data = {
          type: 3,
          areaCode: this.areaCode,
          phone: this.phone
        }
        this.$http.post('/api/common/user/phone/smscode', data, {
          login: true,
          loading: true,
          server: true
        }).then(({ expireTime }) => {
          this.verify = {
            data, expireTime
          }
          this.$router.push({
            name: 'add-phone-verify'
          })
        })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
</style>
