import '@/scss/index.scss'
import 'amfe-flexible'

import Vue from 'vue'

import router from '@/router'
import store from '@/store'
import i18n from '@/i18n'

import cfg from 'config'

import '@/globals.js'

import { log, setEnable } from '@/utils/log.js'
import JsNativeApi, { inject } from '@/utils/js-native-api.js'
import { auth } from '@/utils/auth.js'
import { constant } from '@/utils/common.js'

import App from '@/App.vue'

Vue.config.productionTip = cfg.isDebug
Vue.config.devtools = cfg.isDebug

if (cfg.isDebug) {
  Vue.config.warnHandler = function (msg, vm, trace) {
    console.log(msg, trace)
  }
  Vue.config.errorHandler = function (err, vm, info) {
    console.log(err, info)
  }
  Vue.prototype.$log = window.console.log
} else {
  Vue.prototype.$log = log
  setEnable(true)
}

Promise.all([
  inject(),
  auth()
]).then(() => {
  log(process.env.APP_VERSION)
  JsNativeApi.ready().then(() => {
    if (i18n.locale === 'none') {
      return JsNativeApi.language().then(lang => {
        log(`language is ${ lang }`)
        switch (lang) {
          case 'zh_HK':
          case 'zh_TW':
            i18n.locale = 'sc'
            break
          case 'zh_CN':
            i18n.locale = 'tc'
            break
          default:
            i18n.locale = 'en'
            break
        }
      }, () => {
        i18n.locale = 'en'
      })
    }
  }).then(() => {
    constant()
    new Vue({
      store,
      router,
      i18n,
      render: h => h(App)
    }).$mount('#app')
  })
})
